/* ==========================================================================
   #FONTS
   ========================================================================== */
@font-face {
  font-family: "Gotham Pro";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/GothamPro.woff2") format("woff2"), url("../fonts/GothamPro.woff") format("woff");
}

@font-face {
  font-family: "Gotham Pro";
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: url("../fonts/GothamPro-Black.woff2") format("woff2"), url("../fonts/GothamPro-Black.woff") format("woff");
}

@font-face {
  font-family: "Acrom";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Acrom-Medium.woff") format("woff");
}

/* ==========================================================================
   #RESET
   ========================================================================== */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* ==========================================================================
   #BASE STYLES
   ========================================================================== */
html {
  width: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

@-ms-viewport {
  width: device-width;
}

body {
  min-width: 320px;
  background-color: #fff;
  overflow-x: inherit;
}

hr {
  display: block;
  height: 0;
  border: 0;
  border-top: 1px solid #003a4c;
  margin-top: 3.75rem;
  margin-bottom: 3.75rem;
  padding: 0;
}

@media (min-width: 61.25em) {
  hr {
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
}

@media (min-width: 81.25em) {
  hr {
    margin-top: 7.5rem;
    margin-bottom: 7.5rem;
  }
}

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

a {
  color: #ffc200;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
  text-decoration: none;
  transition: color 0.3s ease-out;
}

a:hover {
  text-decoration: none;
  color: #e0ab00;
}

sup {
  position: relative;
  top: -.5em;
  font-weight: 800;
  font-size: 60%;
  line-height: 0;
  vertical-align: baseline;
  border-bottom: 1px solid currentColor;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  border-style: none;
}

b {
  font-weight: bold;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 900;
  line-height: 1.15;
}

p {
  margin-bottom: 1rem;
}

p:last-child {
  margin-bottom: 0;
}

input, textarea, select, button {
  margin: 0;
  padding: 0;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  border: 0;
  background-color: transparent;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  background-image: url(../img/general/bg-top.png);
  background-repeat: no-repeat;
  background-position: top -150px left 50%;
}

@media (min-width: 46.25em) {
  .wrapper {
    background-position: top 0 right 0;
  }
}

@media (min-width: 90em) {
  .wrapper {
    background-position: top 0 right 20%;
  }
}

.container {
  width: 100%;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: 46.25em) {
  .container {
    padding-left: 24px;
    padding-right: 24px;
  }
}

@media (min-width: 61.25em) {
  .container {
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media (min-width: 81.25em) {
  .container {
    width: 1000px;
    max-width: none;
    padding-left: 0;
    padding-right: 0;
  }
}

.logo {
  display: block;
  text-decoration: none;
}

.logo:hover {
  text-decoration: none;
}

.section .container {
  padding-top: 60px;
  padding-bottom: 60px;
}

.section--fired {
  position: relative;
  overflow: hidden;
}

/* ==========================================================================
   #BASIC TYPOGRAPHY STYLES
   ========================================================================== */
html {
  font-size: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  font-family: "GothamPro", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: normal;
  font-size: 1.125rem;
  line-height: 1.222;
  color: #003a4c;
}

@media (min-width: 46.25em) {
  body {
    font-size: 1.25rem;
  }
}

@media (min-width: 61.25em) {
  body {
    font-size: 1.5rem;
  }
}

@media (min-width: 81.25em) {
  body {
    font-size: 1.875rem;
  }
}

/* ==========================================================================
   #HELPERS & UTILITIES
   ========================================================================== */
.hidden {
  display: none !important;
}

/*
* Hide only visually, but have it available for screen readers
*/
.sr-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

/*
* Extends the .sr-only class to allow the element
* to be focusable when navigated to via the keyboard
*/
.sr-only.focusable:active,
.sr-only.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
  white-space: inherit;
}

/*
* Hide visually and from screen readers, but maintain layout
*/
.invisible {
  visibility: hidden;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.text-bold {
  font-weight: bold !important;
}

.text-center {
  text-align: center !important;
}

.text-highlight {
  color: #ffc200 !important;
}

.text-white {
  color: #fff !important;
}

.text-underline {
  text-decoration: underline;
}

.text-underline::before, .text-underline::after {
  text-decoration: none;
}

.text-line-through {
  position: relative;
  display: inline-block;
}

.text-line-through::after {
  content: '';
  position: absolute;
  top: 56%;
  left: 0;
  width: 100%;
  height: 0;
  border-top: 2px solid #ffc100;
}

.bg-primary {
  background-color: #24bdc8;
}

.bg-dark {
  background-color: #003a4c;
}

.bg-warning {
  background-color: #ffc100;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.mb-1 {
  margin-bottom: 0.25rem;
}

.mb-2 {
  margin-bottom: 0.5rem;
}

.mb-3 {
  margin-bottom: 0.75rem;
}

.mb-4 {
  margin-bottom: 1rem;
}

.mb-5 {
  margin-bottom: 1.25rem;
}

.mb-6 {
  margin-bottom: 1.5rem;
}

.mb-7 {
  margin-bottom: 1.75rem;
}

.mb-8 {
  margin-bottom: 2rem;
}

/* ==========================================================================
   #ACCORDION
   ========================================================================== */
.accordion-item {
  position: relative;
  margin: 0;
}

.accordion-item + .accordion-item {
  margin-top: 1rem;
}

@media (min-width: 61.25em) {
  .accordion-item + .accordion-item {
    margin-top: 1.5rem;
  }
}

.accordion-item__heading {
  font-size: 1.125rem;
  line-height: 1.3;
}

@media (min-width: 46.25em) {
  .accordion-item__heading {
    font-size: 1.25rem;
  }
}

@media (min-width: 61.25em) {
  .accordion-item__heading {
    font-size: 1.5rem;
  }
}

@media (min-width: 81.25em) {
  .accordion-item__heading {
    font-size: 1.875rem;
  }
}

.accordion-item__button {
  position: relative;
  padding-left: 25px;
  text-align: left;
  line-height: 1.2;
  cursor: pointer;
}

@media (min-width: 61.25em) {
  .accordion-item__button {
    padding-left: 40px;
  }
}

.accordion-item__button:focus {
  outline: none;
}

.accordion-item__button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 13px;
  height: 13px;
  transform: translateY(-50%);
  border: 4px solid #ffc100;
  border-radius: 50%;
}

@media (min-width: 61.25em) {
  .accordion-item__button::before {
    width: 26px;
    height: 26px;
    border-width: 6px;
  }
}

.accordion-item__button-more {
  color: #ffc200;
}

.is-active .accordion-item__button-more {
  display: none;
}

.accordion-item__content {
  display: none;
  padding-top: 10px;
  padding-bottom: 10px;
}

.is-active .accordion-item__content {
  display: block;
}

/* ==========================================================================
   #HEADINGS
   ========================================================================== */
.heading {
  position: relative;
  max-width: 700px;
  margin-bottom: 1rem;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 20px;
  font-size: 1.25rem;
}

@media (min-width: 46.25em) and (max-width: 61.24em) {
  .heading {
    font-size: 1.875rem;
  }
}

@media (min-width: 61.25em) {
  .heading {
    margin-bottom: 2rem;
    padding-left: 22px;
    font-size: 2.5rem;
  }
}

.heading--warning::before,
.heading--primary::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 0;
  border-left: 5px solid;
}

@media (min-width: 61.25em) {
  .heading--warning::before,
  .heading--primary::before {
    border-left-width: 7px;
  }
}

.heading--primary::before {
  border-color: #24bdc8;
}

.heading--warning::before {
  border-color: #ffc100;
}

/* ==========================================================================
   #FORM
   ========================================================================== */
.form label.error {
  display: block;
  margin-top: 8px;
  color: red;
  font-size: 0.9375rem;
}

.form input.error {
  border-color: red;
}

/* ==========================================================================
   #BUTTONS
   ========================================================================== */
.btn {
  position: relative;
  display: inline-block;
  text-align: center;
  vertical-align: top;
  user-select: none;
  margin-top: 1.875rem;
  padding: 10px 28px;
  border: 2px solid transparent;
  border-radius: 50px;
  font-family: "Acrom", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.15;
  text-decoration: none;
  background-color: transparent;
  box-shadow: none;
  cursor: pointer;
  transition: background-color 0.3s ease-out, border-color 0.3s ease-out, box-shadow 0.3s ease-out, color 0.3s ease-out;
}

.btn:hover {
  text-decoration: none;
  box-shadow: none;
}

.btn:focus {
  outline: none;
  box-shadow: none;
}

.btn:active {
  box-shadow: none;
}

@media (min-width: 46.25em) {
  .btn {
    font-size: 1.125rem;
  }
}

@media (min-width: 61.25em) {
  .btn {
    margin-top: 2.75rem;
    padding: 12px 40px;
    font-size: 1.375rem;
  }
}

.btn--block {
  display: block;
  width: 100%;
}

.btn--center {
  margin-left: auto;
  margin-right: auto;
}

.btn--primary {
  border-color: #ffc100;
  color: #fff;
  background-color: #ffc100;
  box-shadow: 3px 5px 0 #ebf3f4;
}

.btn--primary:hover {
  color: #ffc200;
  background-color: transparent;
}

.btn--light {
  border-color: #fff;
  background-color: #fff;
  color: #ffc100;
  box-shadow: 3px 5px 0 #1facb6;
}

.btn--light:hover {
  border-color: #ffc200;
  background-color: #ffc200;
  color: #fff;
}

.btn-group {
  text-align: center;
}

@media (max-width: 46.24em) {
  .btn-group {
    text-align: center;
  }
}

/* ==========================================================================
   #LIST-STYLED
   ========================================================================== */
.list-styled li {
  position: relative;
  margin: 0;
  padding-left: 25px;
}

@media (min-width: 61.25em) {
  .list-styled li {
    padding-left: 40px;
  }
}

.list-styled li + li {
  margin-top: 1rem;
}

@media (min-width: 61.25em) {
  .list-styled li + li {
    margin-top: 1.5rem;
  }
}

.list-styled li::before {
  content: '';
  position: absolute;
  top: 0.6em;
  left: 0;
  width: 13px;
  height: 13px;
  transform: translateY(-50%);
  border: 4px solid #ffc100;
  border-radius: 50%;
}

@media (min-width: 61.25em) {
  .list-styled li::before {
    width: 26px;
    height: 26px;
    border-width: 6px;
  }
}

/* ==========================================================================
   #ICON-GROUP
   ========================================================================== */
.icon-group {
  margin-top: 1.5rem;
  font-size: 0;
  text-align: center;
}

@media (min-width: 61.25em) {
  .icon-group {
    margin-top: 2.5rem;
  }
}

.icon-group li {
  display: inline-block;
  margin-right: 10px;
  margin-top: 4px;
  margin-bottom: 4px;
  vertical-align: middle;
}

.icon-group li:last-child {
  margin-right: 0;
}

.icon-wrap {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  font-size: 20px;
  color: inherit;
}

@media (min-width: 61.25em) {
  .icon-wrap {
    width: 48px;
    height: 48px;
    font-size: 24px;
  }
}

.icon-wrap--center {
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

.icon-wrap.is-animated {
  animation-name: bounce;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

@media (min-width: 61.25em) {
  .icon-wrap + .icon-group {
    margin-top: 1.5rem;
  }
}

.svg-icon {
  display: inline-block;
  width: 1.2em;
  height: 1.2em;
}

.svg-icon svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: currentColor;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-12px);
  }
  60% {
    transform: translateY(-5px);
  }
}

/* ==========================================================================
   #INPUT
   ========================================================================== */
.input__wrapper {
  display: block;
  position: relative;
}

.input, .textarea {
  padding: 0 18px;
  height: 50px;
  border-bottom: 3px solid #fff;
  width: 100%;
  box-sizing: border-box;
  font-size: inherit;
  line-height: 1.15;
  border-radius: 0;
  min-height: 0;
  background-color: transparent;
  transition: border-color 0.3s ease-out;
}

.input:focus, .textarea:focus {
  outline: none;
  border-color: #ffc100;
}

@media (min-width: 61.25em) {
  .input, .textarea {
    height: 60px;
  }
}

.input::placeholder, .textarea::placeholder {
  color: #587e8a;
}

.textarea {
  padding-top: 13px;
  padding-bottom: 13px;
  min-height: 150px;
  resize: vertical;
}

/* ==========================================================================
   #TIMER
   ========================================================================== */
.timer {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.timer-chart {
  position: relative;
  width: 74px;
  height: 74px;
}

.timer-chart + .timer-chart {
  margin-left: 15px;
}

.timer-chart + .timer-chart::before {
  content: ':';
  position: absolute;
  top: 50%;
  left: 0;
  padding-left: 5px;
  padding-right: 5px;
  transform: translate(-100%, -50%);
  display: inline-block;
  font-weight: 900;
  font-size: 25px;
  line-height: 1.15;
  color: #003a4c;
}

.timer-chart canvas {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.timer-chart__value {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: 900;
  font-size: 22px;
  line-height: 1.15;
  color: inherit;
  text-align: center;
}

.timer--square .timer-chart {
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 3px 5px 4px #1facb6;
}

/* ==========================================================================
   #SLIDER
   ========================================================================== */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.slider-item:focus {
  outline: none;
}

.slider-item__img {
  text-align: center;
}

.slider-item__img img {
  display: inline-block;
}

.slick-dots > li {
  display: inline-block;
}

.slick-dots > li:only-child {
  display: none;
}

.slider .slick-dots {
  margin-top: 1rem;
  text-align: center;
  font-size: 0;
}

.slider .slick-dots li {
  margin-left: 4px;
  margin-right: 4px;
}

.slider .slick-dots button {
  width: 13px;
  height: 13px;
  border: 4px solid #dedede;
  border-radius: 50%;
  cursor: pointer;
}

.slider .slick-dots button:focus {
  outline: none;
}

.slider .slick-active button {
  border-color: #ffc100;
}

/* ==========================================================================
   #WAVES
   ========================================================================== */
.waves {
  position: relative;
  width: 100%;
  height: 100px;
  margin-bottom: -7px;
  /*Fix for safari gap*/
  min-height: 100px;
  max-height: 100px;
}

@media (max-width: 31.24em) {
  .waves {
    height: 50px;
    min-height: 50px;
  }
}

.waves--top {
  transform: rotate(-0.5turn);
}

/* Animation */
.parallax > use {
  animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}

.waves--top .parallax > use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 9s;
}

.waves--bottom .parallax > use:nth-child(1) {
  animation-delay: -9s;
  animation-duration: 7s;
}

@keyframes move-forever {
  0% {
    transform: translate3d(90px, 0, 0);
  }
  100% {
    transform: translate3d(-85px, 0, 0);
  }
}

/* ==========================================================================
   #FIREFLY
   ========================================================================== */
.firefly {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 10px;
  height: 10px;
  margin: -0.2vw 0 0 9.8vw;
  animation: ease 200s alternate infinite;
  pointer-events: none;
}

.firefly::before, .firefly::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transform-origin: -10vw;
}

.firefly::before {
  background: white;
  opacity: 0.4;
  animation: drift ease alternate infinite;
}

.firefly::after {
  background: white;
  opacity: 0;
  box-shadow: 0 0 0vw 0vw #fff;
  animation: drift ease alternate infinite, flash ease infinite;
}

